@import url(https://fonts.googleapis.com/css2?family=ZCOOL+QingKe+HuangYou&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
body {
    line-height: normal;
}

h1, h2, h3 {
    font-weight: bold;
    text-decoration: underline;
}

h1 {
    font-size: 1.563em;
}

h2 {
    font-size: 1.25em;
}

strong {
    font-weight: bold;
}

.game-container {
    min-height: 100vh;
    min-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.banner {
    position: fixed;
    width: 100%;
    top: 0;
    background-color: lightblue;
    text-align: center;
    padding: 10px 35px;
    box-sizing: border-box;
}

.sidebar {
    background-color: lightblue;
    height: 100%;
    width: 320px;
    right: 0;
    top: 0;
    z-index: 1;
    position: fixed;
    box-sizing: border-box;
    overflow: hidden;
    transition: 350ms;
    display: flex;
    font-family: Consolas, monaco, monospace; 
}

.sidebar-toggle {
    background-color: lightblue;
    color: black;
    padding: 0; 
    width: 25px;
    border: none;
    border-left: 2px solid aliceblue;
    border-right: 2px solid aliceblue;
    font-weight: bold;
    font-family: Consolas, monaco, monospace; 
}

.sidebar-toggle:focus {
    outline: none;
}

.sidebar-nav {
    background-color: lightblue;
    display: flex;
    flex-direction: column;
    width: 100px;
    box-sizing: border-box;
}

.sidebar-nav button {
    background-color: lightblue;
    border: 6px outset aliceblue;
    border-right: none;
    height: 65px;
    font: bold 14px Consolas, monaco, monospace;
}

.sidebar-nav button:focus {
    outline: none;
}

.sidebar-nav .selected {
    background-color: deepskyblue;
}

.sidebar-panel {
    background-color: deepskyblue;
    width: 195px;
    padding: 5px;
    border-left: 10px ridge deepskyblue;
    overflow-y: auto;
    overflow-wrap: break-word;
}

.sidebar-settings-panel {
    display: flex;
    flex-direction: column;
}

.sidebar-settings-panel * {
    margin: 5px;
}

.game {
    display: flex;
    background-color: lightgray;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border: 3px outset;
    padding: 10px;
}

.board {
    background-color: lightgray;
    border: 3px inset;
}

.game-status {
    height: 50px;
    width: 100%;
    background-color: lightgray;
    border: 3px inset;
    margin-bottom: 10px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px;
}

.counter-panel {
    background-color: black;
    color: red;
    height: 28px;
    width: 55px;
    text-align: center;
    font-family: 'ZCOOL QingKe HuangYou', cursive;
    font-size: 28px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.center-panel {
    width: 70px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.panel-tile {
    width: 30px;
    height: 30px;
    box-sizing: border-box;
    background-color: lightgray;
    border: 3px outset;
    display: flex;
    align-items: center;
    justify-content: center;
}

.panel-tile:focus {
    outline: none;
}

.panel-tile:active {
    border: 3px inset;
}

.board-row {
    display: flex;
}

.cell {
    border-right: 1px solid gray;
    border-bottom: 1px solid gray;
    display: flex;
}

/* Tile appearance classes */

.tile {
    background-color: lightgray;
    box-sizing: border-box;
    height: 25px;
    width: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.tile:focus {
    outline: none;
}

.covered {
    border: 3px outset;
}

.covered:active {
    border: 3px inset;
}

.revealed {
    border: none;
}

.exploded {
    background-color: red;
    border: none;
}

/* Inner Tile appearance classes */
.flag {
    background-color: black;
    height: 12.5px;
    width: 2px;
    -webkit-transform: translate(-4px, 0px);
            transform: translate(-4px, 0px);
}

.flag::after {
    content: "";
    width: 0;
    height: 0;
    border-top: 4px solid transparent;
    border-left: 8px solid red;
    border-bottom: 4px solid transparent;
    position: absolute;
    -webkit-transform: translate(1px, 0px);
            transform: translate(1px, 0px);
}

.bomb {
    background-color: black;
    height: 12.5px;
    width: 12.5px;
    border-radius: 50%;
    /* transform: translate(-2px, 0px); */
}

.bomb::after {
    content: "";
    background-color: white;
    height: 4px;
    width: 4px;
    border-radius: 50%;
    position: absolute;
    -webkit-transform: translate(-4px, 2px);
            transform: translate(-4px, 2px);
}

.number {
    font: bold 16px "Arial Black", Gadget, sans-serif;
    text-align: center;
}

.one {
    color: blue;
}

.two {
    color: green;
}

.three {
    color: red;
}

.four {
    color: purple;
}

.five {
    color: maroon;
}

.six {
    color: turquoise;
}

.seven {
    color: black;
}

.eight {
    color: gray;
}
